/* общее */
.form1 {
  padding-top: 50px;
  padding-bottom: 30px;
  background: var(--color-bg);
}

/* прямоугольник */
.box {
  max-width: 1640px;
  height: 609px;
  margin: 0 auto;
  background: var(--color-bg);
  border: 2px solid var(--color-primary);
  border-radius: 40px;
  display: grid;
  grid-template-columns: 752px 520px;
  gap: 227px;
  align-items: start;
}

/* текст */
.title {
  font-family: var(--font-main);
  font-size: var(--font-size-h2);
  font-weight: 500;
  max-width: 752px;
  margin-bottom: 28px;
  margin-left: 40px;
  color: var(--color-text);
}

.greny {
  color: var(--color-primary);
}

.text {
  font-family: var(--font-menu);
  font-size: var(--font-size-base);
  color: var(--color-text);
  max-width: 466px;
  margin-left: 40px;
  font-weight: 200px;
}

/* форма */
.form {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-top: 37px;
}

.row {
  position: relative;
}

.row input,
.row textarea {
  width: 456px;
  font-family: var(--font-menu);
  font-size: 14px;
  padding: 20px 30px;
  border: 2px solid transparent;
  border-radius: 50px;
  background: var(--color-accent);
  transition: border-color 0.2s;
  resize: vertical;
  margin-top: 5px;
}

.row input:focus,
.row textarea:focus {
  outline: none;
  border-color: var(--color-primary);
}

.row textarea {
  border-radius: 30px;
  height: 80px;
}

.label {
  font-family: var(--font-menu);
  color: var(--color-text);
  font-size: 20px;
}

.check {
  display: flex;
  align-items: top;
  gap: 10px;
  cursor: pointer;
  font-family: var(--font-menu);
  font-size: 10px;
  color: var(--color-text);
}

.check input {
  width: 18px;
  height: 18px;
}

.btn {
  width: 520px;
  height: 64px;
  font-family: var(--font-menu);
  font-size: 20px;
  font-weight: 400;
  color: var(--color-text);
  background: var(--color-bg);
  border: 1px solid var(--color-accent);
  border-radius: 50px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
}

.btn:hover:not(:disabled) {
  background: var(--color-primary);
  border: 2px solid transparent;
  color: var(--color-text2);
}

.btn:disabled {
  opacity: 0.7;
  cursor: not-allowed;
}

/* адаптив 1200-800-375 */
@media (max-width: 1900px) {
  .form1 {
    padding-top: 40px;
  }

  .box {
    max-width: 1120px;
    gap: 87px;
    grid-template-columns: 577px 414px;
  }

  .title {
    max-width: 577px;
  }

  .row input,
  .row textarea {
    width: 350px;
  }

  .btn {
    width: 414px;
  }
}

@media (max-width: 1200px) {
  .box {
    max-width: 760px;
    grid-template-columns: 338px 334px;
    gap: 32px;
  }
  
  .title {
    font-size: 36px;
  }

  .text {
    font-size: 20px;
  }

  .row input,
  .row textarea {
    width: 298px;
  }

  .btn {
    width: 334px;
  }
}

@media (max-width: 800px) {
  .form1 {
    padding-top: 0px;
  }

  .box {
    max-width: 335px;
    height: 744px;
    grid-template-columns: 1fr;   
    gap: 0px; 
    border-radius: 20px;
  }

  .title {
    max-width: 310px;
    font-size: 28px;
    margin-left: 10px;
  }

  .text {
    margin-left: 10px;
    font-size: 16px;
    margin-bottom: 0px;
  }

  .form {
    margin-left: 10px;
  }

  .row input,
  .row textarea {
    padding: 10px 15px;
    width: 281px;
  }
  
  .btn {
    width: 315px;
    height: 40px;
  }
}